.usertoken{
    padding: 82px 0px;
    background: url("../../../../assets/banner1.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    .mainheading{
            h2 {
                color: var(--White, #FFF);
                font-size: 40px;
                font-style: normal;
                font-weight: 600;
                line-height: 120%;
                max-width: 536px;
            }
    
            p {
                color: var(--White, #FFF);
    
                margin-top: 18px;
                margin-bottom: 58px;
                font-size: 16px;
                font-style: normal;
                font-weight: 400;
                line-height: 120%;
            }
    
            button {
                border-radius: 40px;
                background: var(--Button-Primery-Gradient, linear-gradient(180deg, #3456D0 0%, #0D32B7 100%));
                padding: 17px 36px;
                // max-width: 173px;
                // width: 100%;
                // overflow: hidden;
                color: var(--White, #FFF);
                // text-overflow: ellipsis;
                font-size: 14px;
                font-style: normal;
                font-weight: 500;
                line-height: 120%;
                border: none;
                white-space: nowrap;
            }
        }
    }
